/*============END GENERAL================*/

/*============HEADER================*/
#header{
   height: 80px;border-bottom: 1px solid #eaeaea;
   .store-ads{
      display: inline-block;margin-top: 40px;
      a{font-size: 12px;color: #999;margin-right: 20px;float: left;
         .icon-ns{float: left;margin: -1px 2px 0 0;width: 14px;height: 14px;}
         &.ads{
            .icon-ns{color: #c6d44d; background: #588b98;}
         }
         &.store {
          .icon-ns {
            color: $colormain;
          }
         }
      }
   }
   .logo{
       display: inline-block;
       width: 150px;
       position: absolute;
       left: 50%;
       top: 10px;
       transform: translate(-50%, 0%);
       -webkit-transform: translate(-50%, 0%);
       -moz-transform: translate(-50%, 0%);
       -ms-transform: translate(-50%, 0%);
   }
   #search{
      float: right;position: relative;margin-top: 32px;
      input{
         font-size: px_to_rem(12px);
         border: 1px solid #dfdfdf;
         height: 34px;
         width: 140px;
         padding: 0 15px;
         outline: 0;
         border-radius: 3px;
         -webkit-border-radius: 3px;
         -moz-border-radius: 3px;
      }
      button{
          position: absolute;
          top: 7px;
          right: 10px;
          border: 0;
          background: none;
         .icon-ns{color: #0064b2;width: 20px;height: 20px;}
      }
   }
}
/*============END HEADER============*/

/*============Header sticky=========*/
#header_sticky{
  height: 44px;
  background: #fff;
  border-bottom: 1px solid #eaeaea;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 300; 
  -webkit-transform: translateY(-100%);
  -ms-transform: translateY(-100%);
   transform: translateY(-100%);
   @include cubic-transition($delay: 0, $duration: 350ms, $property: (transform));
   will-change: transform;
   &.show_sticky{
     -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      transform: translateY(0);
      .inner_section{padding-right: 0;}
   } 
   .logo{
      width: 87px;float: left;margin: 3px 30px 0 0;
   }
   nav{
      float: left;
      a{
         font: bold px_to_rem(14px) $fontmain;line-height: 44px;display: inline-block;padding: 0 7px;

      }
   }
   .store-ads{
      float: right;margin-top: 12px;
      a{margin-right: 8px;
         .icon-ns{width: 14px;height: 14px;margin: 0;}
         &.ads{
            .icon-ns{color: #c6d44d}
         }
      }
      .search-btn{
          float: right;margin-top: 3px;
         .icon-ns{
             position: relative;
             top: 0;
             cursor: pointer;
             color: #0064b2; 
             display: block; 
             -webkit-transition-duration: 400ms;
             transition-duration: 400ms;
             -webkit-transition-property: all;
             transition-property: all;
             -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
             transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
             &.active{opacity: 0;}           
         }
         &.is-clicked{
            .icon-ns{
               top: -13px;opacity: 0;
               &.active{top: -15px;opacity: 1;}
            }
         }
      }
   }
   .search-field{
       position: absolute;
       top: 100%;
       left: 0;
       display: none;
       z-index: 9;
       width: 100%;
       background: #fff;
       border-top: 1px solid #ebeef6;
       border-bottom: 1px solid #ebeef6;
       input{
          width: 100%;
          font-size: px_to_rem(18px);
          color: #34343c;
          background: #fff;
          border: none;
          -webkit-box-shadow: none;
          box-shadow: none;
          padding: 15px 0;
          outline: 0;            
       }         
   }        
}
/*==========End Header sticky=========*/

