/*breadcrumb*/
#breadcrumb{
  margin: 20px auto 15px;  
  & + section {
    .box_300 {
      margin-top: -50px;
    }
  }
  ul{
    width: calc(100% - 348px);
    float: left;
    min-height: 36px;
    li{
      display: inline-block;margin-right: 20px;font: 400 px_to_rem(16px)/#{1.4} arial;position: relative;
      h1, label{font:bold px_to_rem(28px)/#{1.4} $fontmain2; display: inline-block; a{color: #333}}
      h2, h3 {
        font: 400 px_to_rem(16px)/#{1.4} arial; 
        display: inline-block;
      }
      .icon-ns{margin: 0px 0 0 20px;width: 13px;height: 13px;}
      a{color: #999;}
      &:first-of-type {
        h2 {
          font:bold px_to_rem(28px)/#{1.4} $fontmain2; display: inline-block; a{color: #333}
        }
      }
      &.c2{
        a{color: $colormain;display: inline-block;line-height: 25px;}
        .icon-ns{margin-left: 5px;}
        &:hover{
          .sub_menu{
            opacity: 1;
            clip: inherit;
            -webkit-transform: scaleY(1);
            -khtml-transform: scaleY(1);
            -moz-transform: scaleY(1);
            -ms-transform: scaleY(1);
            -o-transform: scaleY(1);
            transform: scaleY(1);            
          }

        }
      }
      &.c3{
        padding-left: 30px;
        &:before{
          content: "";
          background: #dfdfdf;
          width: 1px;
          height: 31px;
          position: absolute;
          left: 0;
          bottom: -2px;
        }
        a{margin-right: 20px;}
      }
      .sub_menu{
        background: #fff;
        position: absolute;
        top: 25px;
        left: -10px;
        z-index: 3;
        border: 1px solid #dfdfdf;
        min-width: 120px;
        -webkit-transform: scaleY(0);
        -khtml-transform: scaleY(0);
        -moz-transform: scaleY(0);
        -ms-transform: scaleY(0);
        -o-transform: scaleY(0);
        transform: scaleY(0);
        transform-origin: 0 0 0;
        -webkit-transform-origin: 0 0 0;
        -ms-transform-origin: 0 0 0;
        -o-transform-origin: 0 0 0;
        -webkit-transition: all 0.4s ease 0s;
        -moz-transition: all 0.4s ease 0s;
        -ms-transition: all 0.4s ease 0s;
        transition: all 0.4s ease 0s;                
        a{display: block;padding: 5px 15px;color: #333;
          &:hover{background:$colormain;color: #fff}
        }
      }
    }
  }
}
#breadcrumb ul li label {
  h1 {
    color: $colormain; margin-left: 10px;
  }
}
.top-story{
  font-size:px_to_rem(0px);margin: 0 0 28px -28px;border-bottom: 1px solid #eaeaea;
