/*FONT FAMILY*/
@font-face {
    font-family: 'iCielDomaineText';
    src: url('fonts/iCielDomaineText-Bold.eot');
    src: url('fonts/iCielDomaineText-Bold.eot?#iefix') format('embedded-opentype'),
        url('fonts/iCielDomaineText-Bold.woff2') format('woff2'),
        url('fonts/iCielDomaineText-Bold.woff') format('woff'),
        url('fonts/iCielDomaineText-Bold.ttf') format('truetype'),
        url('fonts/iCielDomaineText-Bold.svg#iCielDomaineText-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
}
