/*Mail Popup*/

#tile_lightbox_vne_cor {
	background: url(images/graphics/logo_lightboxv2.png) 20px center no-repeat #f5f5f5;
	color: #333;
	font: 700 16px arial;
	padding: 5px 5px 10px;
	float: left;
	width: 100%;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	border-bottom: 1px solid $colormain;
}
.txt_lightbox_right_vne_cor {
	padding-right: 10px;
	padding-top: 10px;
	float: right;
}
#form_sendemail {
	table {
		tr {
			float: left;
			width: 100%;
			padding-bottom: 5px;
			input {
				margin-right: 10px;
				float: left;
				width: 100%;
				border: 1px solid #ddd;
				padding: 5px;
				height: 30px;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
				font: 12px arial;
			}
			textarea {
				border: 1px solid #ddd;
				padding: 5px;
				height: 30px;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
				font: 12px arial;
				min-height: 100px;
				width: 100%;
			}
		}
		td.txt_right {
			width: 100%;
			margin-right: 20px;
			vertical-align: top;
			padding-top: 5px !important;
		    padding-bottom: 3px;
			float: left;
			font-size: 15px;
		}
		td.w400 {
			width: 100%;
			float: left;
			margin-right: 10px;
		}
	}
	form {
		padding: 10px 30px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
	tr.mabaomat {
		input {
			width: 60%;
			float: left;
		}
	}
}
