#box_comment {
	.txt_blue {
		&:hover {
			b {
				text-decoration: underline;
			}
		}
	}
	width: 100%;
	float: left;
	margin-bottom: 20px;
	.title_show {
		.ykien_vne {
			border-bottom: 1px solid #dfdfdf;
			padding-bottom: 5px;
			margin-bottom: 15px;
		}
		.txt_songuoithich {
			color: #666;
			font: 400 11px/26px Roboto,sans-serif;
		}
	}
	.comment_item {
		p {
			padding-bottom: 2px;
			line-height: 20px;
			font-size: 14px;
			font-family: Arial;
		}
		p.block_like_web {
			float: left;
			width: 100%;
		}
		margin-bottom: 10px;
		border-bottom: 1px solid #f4f4f4;
		.sub_comment {
			padding: 0;
			margin-top: 10px;
			.subcomment_item {
				background: #f4f4f4;
				padding: 8px 15px !important;
				margin: 0 !important;
				position: relative;
				&:first-child {
					&:before {
						content: "";
						border-left: 10px solid transparent;
						border-right: 10px solid transparent;
						border-bottom: 10px solid #f4f4f4;
						position: absolute;
						top: -9px;
						left: 10px;
					}
					border-top: 0;
					padding-top: 0;
				}
			}
		}
		.user_status {
			.left.txt_11 {
				font-size: 12px;
				padding: 5px 0 0;
				white-space: nowrap;
			}
		}
		.txt_view_more {
			padding: 0;
			background: none;
			margin: 10px 0;
			text-align: center;
			a {
				color: #00559a;
				font-size: 14px;
				text-decoration: underline;
			}
		}
	}
	.hight_light {
		p.txt_666.txt_11 {
			margin-right: 0;
		}
	}
	.border_item {
		border-bottom: 1px dotted #ccc;
		margin-bottom: 10px;
		padding-bottom: 10px;
	}
	.sub_comment {
		float: right;
		width: 100%;
		position: relative;
		padding: 0 0 0 20px;
		.input_comment {
			padding: 0 0 10px;
			background: none;
			margin-bottom: 10px;
			border-bottom: 1px dotted #e2e2e3;
		}
	}
	.subcomment_item {
		padding-top: 10px;
		margin-bottom: 10px;
		border-top: 1px dotted #e2e2e3;
	}
	.counter_world {
		font: 400 11px Roboto,sans-serif;
		color: #666;
	}
	a.link_thich {
		margin-right: 0;
	}
	a.txt_666.txt_11 {
		color: #059;
	}
	.view_more_coment {
		width: 100%;
		float: left;
		text-align: center;
		a {
			font-size: 14px;
			color: #059;
			padding: 10px 20px;
			display: block;
			border: 1px solid #dfdfdf;
			font-weight: 500;
			&:hover {
				background: #0064b2;
				color: #fff;
			}
		}
	}
	.block_input_comment {
		border: none !important;
		padding: 0 !important;
		margin-top: 10px;
	}
}

