.box_300{
   .title_box_category{
     h2{
       font: bold px_to_rem(24px)/#{1.4} $fontmain2; padding-right: 15px; 
     }
     &:before{
       content:"";
       position: absolute;
       width: 100%;
       height: 2px;
       bottom: 8px;    
       background-image: -moz-linear-gradient(left, #0799ff #9dfeaf);
       background-image: -webkit-gradient(linear, left top, right top, from(#16d1f2), to(#9dfeaf));
       background-image: -webkit-linear-gradient(left, #0799ff, #9dfeaf);
       background-image: -o-linear-gradient(left, #0799ff, #9dfeaf);  
     }
   }
   .line2{
      .title_box_category{
        &:before{  
          background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
          background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
          background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
          background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
        }
      }
   }
   .line3{
      .title_box_category{
        &:before{  
          background-image: -moz-linear-gradient(left, #8419fd #57b2fb);
          background-image: -webkit-gradient(linear, left top, right top, from(#8419fd), to(#57b2fb));
          background-image: -webkit-linear-gradient(left, #8419fd, #57b2fb);
          background-image: -o-linear-gradient(left, #8419fd, #57b2fb);
        }
      }
   }   
   .box_quangcao {
      margin-bottom: 15px;
   }
   #box_tag {
    padding-left: 10px;
    .txt_tag {
      margin-left: -10px;
    }
   }
}
.box_folder{
   .art_item{
      margin-bottom: 15px;padding-bottom: 15px;border-bottom: 1px solid #eaeaea;
      .thumb_art{margin: 0;float: none;width: 100%}
      .content{
         margin-top: 0;
         .title_news{font: 400 px_to_rem(16px)/#{1.4} $fontmain;margin-bottom: 0;}
      }
      &.img{
         border-bottom: 0;
         .thumb_art{background: #eee;padding: 13px;width: 100%;margin: 0;float: none;}
         .content{
            margin-top: 10px;
            .title_news{font: 500 px_to_rem(20px)/#{1.4} $fontmain;}
         } 
         &:before {
          content: "";
          background: #eee;
          position: absolute;
          width: 150px;
          height: 6px;
          bottom: -3px;
         }                 
      } 
      &:last-child{
         border-bottom: 0;padding-bottom: 0;margin-bottom: 0;
      }     
   }
   .taitro{
      margin-top: 10px;
      a{color: #999;font: 400 12px/#{1.4} arial}
   }
}
/*box_taitro*/
.box_taitro{
   .art_item{
      .thumb_art{margin: 0 15px 0 0;float: left;width: 85px;}
      .content{
         margin-top: 0;
         .title_news{font: 400 px_to_rem(16px)/#{1.4} $fontmain;}
      }   
   }   
}
/*box_thuongmai*/
.box_thuongmai{
  margin-bottom: 28px;
   .title_box_category{
      margin: 10px 0 15px 0;
      &:before{display:none;}
   }
   .owl-nav{
      &>div{
          position: absolute;
          top: -54px;
          width: 38px;
          height: 38px;
          line-height: 32px;
          text-align: center;
          border: 2px solid rgba(12, 164, 254, 0.75);
          border-radius: 4px;
          -webkit-border-radius: 4px;
          -moz-border-radius: 4px;           
         .icon-ns{
            color: #0ca4fe;
            margin: 0;
         }
         &:hover{
            border: 0;
            line-height: 37px;   
            background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
            background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
            background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
            background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
            .icon-ns{color: #fff}            
         }
      }
      .owl-prev{right: 42px;}
      .owl-next{right: 0}
   }     
   .art_item{
      padding-bottom: 15px;
      .thumb_art{margin: 0 15px 0 0;float: left;width: 85px;}
      .content{
         margin-top: 0;
         .title_news{font: 400 px_to_rem(16px)/#{1.4} $fontmain;}
      }   
   }   
}

.box-tieudiem{
   .art_item{
      margin-bottom: 50px;
      .thumb_art{
          background: #eee;
          padding: 13px;
          width: 100%;
          margin: 0;
          float: none; 
          &:before{
            content: "";
            position: absolute;
            left: 13px;
            right: 13px;
            height: 3px;
            top: 10px;
            background-image: -moz-linear-gradient(left, #16d1f2 #9dfeaf);
            background-image: -webkit-gradient(linear, left top, right top, from(#16d1f2), to(#9dfeaf));
            background-image: -webkit-linear-gradient(left, #16d1f2, #9dfeaf);
            background-image: -o-linear-gradient(left, #16d1f2, #9dfeaf);
          }        
      }
      .content{
         width: 100%;
         margin-top: 10px;
         .title_news{font: 500 px_to_rem(20px)/#{1.4} $fontmain;}
      }
      &:before{
       content: "";
       background: #eee;
       position: absolute;
       width: 150px;
       height: 8px;
       bottom: -25px;         
      }
      &:nth-child(2){
         .thumb_art{
            &:before{
             background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
             background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
             background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
             background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
            }            
         }
      }
      &:nth-child(3){
         .thumb_art{
            &:before{
             background-image: -moz-linear-gradient(left, #8419fd #57b2fb);
             background-image: -webkit-gradient(linear, left top, right top, from(#8419fd), to(#57b2fb));
             background-image: -webkit-linear-gradient(left, #8419fd, #57b2fb);
             background-image: -o-linear-gradient(left, #8419fd, #57b2fb);
            }            
         }
      }
      &:last-child{margin-bottom: 20px;}          
   }
}

#box_storens {
  margin-bottom: 28px;
}