@media screen and (min-device-width: 1600px) and (max-device-width: 2400px) {
	
}

@media (max-width: 1100px){
	.container{padding: 0 10px;}
	.featured{
		.art_topstory{width: calc(100% - 300px);}
		.sub_featured{width: 300px;}
	}
	.box_stream{width: 65%;width: calc(100% - 348px);}
	.box_tieudiem{
		padding-bottom: 36px;
		.list_news{width: calc(100% - 254px)}
	}
	.box_quote_home{
		.art_item{
			.warp{
				color: #fff;
			    text-shadow: 1px 1px 1px #333;
			    background-image: -moz-linear-gradient(left, #8419fd #57b2fb);
			    background-image: -webkit-gradient(linear, left top, right top, from(#8419fd), to(#57b2fb));
			    background-image: -webkit-linear-gradient(left, #8419fd, #57b2fb);
			    background-image: -o-linear-gradient(left, #8419fd, #57b2fb);
			    .ava{bottom: 0;right: 0}
			}
			&:nth-child(2){
				.warp{
				    background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
				    background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
				    background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
				    background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
				}
			}
			&:nth-child(3){
				.warp{
				    background-image: -moz-linear-gradient(left, #16d1f2 #9dfeaf);
				    background-image: -webkit-gradient(linear, left top, right top, from(#16d1f2), to(#9dfeaf));
				    background-image: -webkit-linear-gradient(left, #16d1f2, #9dfeaf);
				    background-image: -o-linear-gradient(left, #16d1f2, #9dfeaf);					
				}
			}			
		}
	}
}

@media (min-width: 768px) and (max-width: 991px) {
	.detail_live.page_detail #breadcrumb {
		padding: 0 10px !important;
	}
	.page_detail #breadcrumb {
	    padding: 0 10px;
	}
	.input-search h2 {
		width: 100% !important; 
		margin-bottom: 10px;
	}
	#search_page .form_search .txt-search {
		width: 296px !important;
	}
}

@media (min-width: 992px) and (max-width: 1280px) {
	#search_page .form_search .txt-search {
		width: 296px !important;
	}
}

@media (max-width: 1099px){
	#header_sticky{
		.logo{margin: 3px 10px 0 0;}
		nav{
		    width: 80%;
		    width: calc(100% - 180px);
		    overflow-x: scroll;
		    -webkit-overflow-scrolling: touch;
		    white-space: nowrap;
		    -webkit-backface-visibility: hidden;
		    backface-visibility: hidden;
		    position: relative;
		}
		.store-ads{
			position: relative;
		    &:before{
			    content: "";
			    background: rgba(23, 143, 214, 0.5);
			    position: absolute;
			    width: 1px;
			    height: 20px;
			    left: -9px;
			    top: 0;
		    }			
		}
	}
	#main_menu{
		a{padding: 0 8px;}
	}
	.featured{
		.art_topstory{
			.thumb_big{
				&:before{height: 290px;}
			}
		}
	}
	.art_item{
		.thumb_art{width: 240px;}
	}
	.box_video_home{
		.thumb_big{
			&:before{height: 400px;width: 80px;top: 25px;right: 30px;}
		}
	}
}

@media (max-width: 900px){
	#main_menu{
	    overflow-x: scroll;
	    -webkit-overflow-scrolling: touch;
	    white-space: nowrap;
	    -webkit-backface-visibility: hidden;
	    backface-visibility: hidden;
	    position: relative;		
	}
	.featured{
		.art_topstory{
			.thumb_big{
				padding-left: 0;
				&:before{display:none;}
			}
		}
		.list_news{
			.art_item{width: 33.33%;
				&:nth-child(6){display:inline-block;}
			}
		}
	}	
	.art_item{
		.thumb_art{width: 240px;margin-right: 15px;margin-bottom: 10px;}
		.content{margin-top: 0;}
	}
	.box_video_home .thumb_big{margin-bottom: 10px;}
	.box_quote_home{
		margin-left: -10px;
		&:before{left:10px;}
		.art_item{
			padding-left: 10px;
			.warp{padding:30px 5px 5px 5px;}
			.name{padding-right: 50px;margin-top: 30px;}
		}
	}
}	

@media (max-width: 768px){
	.featured{
		.art_topstory{width: 100%;padding:0;margin-bottom: 20px;
			.thumb_big{width: 60%;float: left;margin-right: 20px;}
			.content{overflow: hidden;margin: 0;
		      	.title_news{
		        	font-size: px_to_rem(24px);
		      	}
			}
		}
		.sub_featured{
			width: 100%;
			.art_item{
				.thumb_art{width: 150px;float: left;
					.tag{
					    bottom: 8px;
					    left: 8px;
					    padding: 4px;						
					}
				}
				.content{
				    width: auto!important;
				    overflow: hidden;
				    float: none;
				    padding: 10px 0 0 15px!important;
				    margin: 0!important;
				}
				&:nth-child(1){
					border-bottom: 1px solid #eaeaea;margin-bottom: 15px;
					&:before{display:none;}
					.thumb_art{
						width:150px;float: left;
						.thumb_5x3{padding-bottom: 100%;}
					}
				}
				
			}
		}
	}
	.box_stream{width: 100%;margin-bottom: 30px;}
	.box_300{width: 100%;padding: 0;}
	.box_video_home{
		.thumb_big{
			padding: 0;
			&:before{display:none;}
		}
	}
	footer{
		.row_menu{
			margin-left: -30px;
			ul{
				padding-left: 30px;
				h3{font-size: px_to_rem(20px)}
			}
		}
	}
	.copy-right{
		.logo{width: 150px;margin: 20px 20px 0 0;}
	}
}

